.calculator {
  position: absolute;
  top: 30%;
  left: 40%;
  width: fit-content;
  height: fit-content;
  background-color: rgb(213 213 213);
  padding: 10px;
  border-radius: 20px;
}

.resultField {
  padding: 10px;
  border-radius: 15px;
  border: 0;
  box-shadow: 4px 4px 10px rgb(0 0 0 / 6%);
  margin-bottom: 10px;
  font-size: 13px;
}

.buttonBox {
  width: 100%;
  height: calc(100% - 110px);
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 8px;
}

.mainButton {
  border: none;
  background-color: rgb(0 0 116);
  font-size: 30px;
  color: rgb(255, 255, 255);
  font-weight: bold;
  cursor: pointer;
  border-radius: 10px;
  outline: none;
}

.subButton {
  border: none;
  background-color: rgb(131, 131, 134);
  font-size: 30px;
  color: rgb(255, 255, 255);
  font-weight: bold;
  cursor: pointer;
  border-radius: 10px;
  outline: none;
}

.subButton:hover {
  background-color: rgb(167, 167, 167);
}