.mainWatch {
    position: absolute;
    top: 40%;
    left: 40%;
    margin-top: -50px;
    margin-left: -50px;
    width: fit-content;
    height: fit-content;
    background-color: darkcyan;
    box-shadow: 0px 6px 17px 5px grey;
    border-radius: 20px;
    padding: 15px;
}
.watchHead{
    display: flex;
    align-items:center;
    color: beige;
}
.ChangeTimer {
    font-size: 40px;
    background-color: darkcyan;
    padding: 15px;
    border-radius: 20px;

}